import { graphql, useStaticQuery } from 'gatsby';

export interface IFriendsSuccess {
  results: IFriendsData[];
}

export interface IFriendsError {
  error: unknown;
}

interface IFriendsData {
  id: number;
  photo: string;
  description: string;
}

const friends: IFriendsData[] = [
  {
    id: 1,
    photo: 'Browar-Piwojad.png',
    description:
      'Piwojad to kontraktowy browar rzemielniczy z Krakowa zaoony przez Pawa widniewicza i Pawa Teodorczyka, ktrzy przygod z piwowarstwem zaczli od domowej produkcji na ma skal. Piwojad du wag przywizuje do regionu, w ktrym warzy piwa. Priorytetem jest zaopatrywanie si w surowce dostpne u lokalnych dostawcw i producentw ywnoci.',
  },
  {
    id: 2,
    photo: 'logo_radziemska.png',
    description:
      'Rodzime przedsibiorstwo chemiczne powstae w 1982 roku. Firma specjalizuje si w produkcji chemii gospodarczej, w tym odplamiaczy, proszkw do prania czy wybielaczy. Jest laureatem wielu nagrd konsumenckich, a jej siedziba znajduje si w omiankach pod Warszaw.',
  },
  {
    id: 3,
    photo: 'AmeriPol-Trading.png',
    description:
      'Firma Ameri-Pol Trading Ltd sp. z o.o. jest polsk firm zaoon w 1992 r. jako spka polsko amerykaska, jednak w cigu lat dziaalnoci udziaowiec amerykaski zosta wykupiony przez polskich udziaowcw. Firma oferuje szeroki wybr klejw kontaktowych w pojemnikach cinieniowych oraz rozpuszczalnikw majcych zastosowanie w rnych branach, m.in. w meblarstwie, w brany motoryzacyjnej, w produkcji jachtw, w brany budowlanej. Oferowane przez nas kleje s sprzedawane pod wasn wspln mark SPRAY-KON i LEP-KON.',
  },
  {
    id: 4,
    photo: 'AvetPharma.png',
    description:
      'Spka farmaceutyczna zaoona 10 lat temu przez magistra farmacji z wieloletnim dowiadczeniem w brany lekw OTC oraz suplementw diety. Firma zajmuje si produkcj oraz dystrybucj suplementw diety. Przez dekad wprowadzia na rynek ponad 40 produktw wasnych, ktre s dostpne w aptekach, sklepach zielarsko-medycznych oraz marketach. Siedziba spki znajduje si w Warszawie.',
  },
];

export const FriendsService = {
  getFriends: async (): Promise<IFriendsSuccess> => {
    return {
      results: friends,
    };
  },
  getAll: () =>
    useStaticQuery(
      graphql`
        {
          allLogosFriendsYaml {
            nodes {
              id
              name
              description
              image {
                base
              }
              page
              slug
            }
          }
        }
      `
    ),
};

export interface IFriendNode {
  id: string;
  name: string;
  description: string;
  image: {
    base: string;
  };
  page: string;
  slug: string;
}
